p {
  font-size: 14pt;
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 300;
  margin: 25px;
  text-align: justify;
  line-height: 130%;
}

a {
  color: #07101d;
  font-style: italic;
  /* font-weight: 400; */
  text-decoration: none;
}

.byline {
  position: relative;
  z-index: 9999;
}

h4 {
  font-size: 16pt;
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 300;
  font-style: italic;
  margin: 0.5em 1.5em;
  text-align: left;
}

h5 {
  font-size: 12pt;
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 200;
  font-style: italic;
  margin: 0.5em 1.5em;
  text-align: left;
  line-height: 150%;
}

.price > p {
  font-size: 22pt;
  margin: 0 25px;
}

h5 > a {
  /* font-weight: 400; */
  color: #4c4c4c;
}

a {
  /* font-weight: 400; */
  color: #4c4c4c;
}

@media only screen and (min-width: 990px) {
  .article {
    width: 50vw;
    margin-left: 1vw;
    margin-top: 2vw;
  }

  .byline {
    /* width: 50vw; */
    margin-left: 1vw;
    /* margin-bottom: 5vw; */
  }

  h4 {
    margin: 25px;
  }
}
